import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
/** Style **/
import styled from 'styled-components';
import * as palette from '../layouts/scss/variables'
import 'semantic-ui-css/semantic.css'
import { Sidebar, Segment, Button, Menu, Image, Icon, Header } from 'semantic-ui-react'
import './main.scss'
/** Components **/
import C_Footer from '../components/Footer/Footer'
import C_Header from '../components/Header/Header'

const propTypes = {
  data: PropTypes.object.isRequired,
}

const Main_Container = styled.div`
  background-color: ${palette.BG_MAIN};
`;

const Burger = styled.button`
  background-color: transparent;
  position: absolute;
  top: 10px;
  right: 15px;
`;

export default class TemplateWrapper extends React.Component {
  state = { visible: false }
  
  toggleVisibility = () => this.setState({ visible: !this.state.visible })
  
  toggleBurger () {
      document.getElementsByClassName('C--menu-burger').classList.toggle('is-active')    
      alert(888)
  }
  
  render() {
    const { visible } = this.state
    const { children } = this.props;

    
        return (
        
          <div>
              <Helmet
                  meta={[
                  { name: 'description', content: 'Sample' },
                  { name: 'keywords', content: 'sample, something' },
                  ]}
              />
              <C_Header />
              <Burger type="button" className="C--menu-burger cmb__anim" onClick={this.toggleVisibility}{...this.toggleBurger}>
                <span className="cmb__box">
                  <span className="cmb__inner"></span>
                </span>
              </Burger>
              <Sidebar.Pushable as={Segment}>
              <Sidebar as={Menu} animation='push' direction='top' visible={visible} inverted>
                  <Menu.Item name='home'>
                  <Icon name='home' />
                  <Link to="/">Accueil</Link>
                  </Menu.Item>
                  <Menu.Item name='gamepad'>
                  <Icon name='gamepad' />
                  <Link to="/works/">Ralisations</Link>
                  </Menu.Item>
                  <Menu.Item name='camera'>
                  <Icon name='camera' />
                  <Link to="/blogue/">Blogue</Link>
                  </Menu.Item>
              </Sidebar>
              <Sidebar.Pusher>
                  <Segment basic>
                  <Main_Container className={'ui container'}>
                      {children()}
                  </Main_Container>
                  <C_Footer />
                  </Segment>
              </Sidebar.Pusher>
              </Sidebar.Pushable>
          </div>
        )
        TemplateWrapper.propTypes = {
            children: PropTypes.func,
          }
      }
    }





